import React from "react";
import s from "./Navbar.module.css"
import {NavLink} from "react-router-dom";

const Navbar = () => {
    return (
        <div>
            <nav className={s.nav}>
                    <NavLink to="/profile" className={navData => navData.isActive ? s.active : s.item}>Profile</NavLink>
                    <NavLink to="/dialogs" className={navData => navData.isActive ? s.active : s.item}>Dialogs</NavLink>
                    <NavLink to="/users" className={navData => navData.isActive ? s.active : s.item}>Users</NavLink>
                    <NavLink to="/news" className={navData => navData.isActive ? s.active : s.item}>News</NavLink>
                    <NavLink to="/music" className={navData => navData.isActive ? s.active : s.item}>Music</NavLink>
                    <NavLink to="/settings"
                             className={navData => navData.isActive ? s.active : s.item}>Settings</NavLink>
                    <NavLink to="/p5js"
                             className={navData => navData.isActive ? s.active : s.item}>P5js</NavLink>
            </nav>
            </div>
    );
};

export default Navbar;
